@use '../foundation/color' as *;

/* l-header
--------------------------- */
.l-header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 89px;
	color: $cWhite;
	padding: 0 30px;
	z-index: 100;
	transition: all 0.3s;

	&__inner {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__ul {
		display: flex;
	}
	&__li {
		font-size: 14px;
		font-weight: bold;
		text-transform: uppercase;
		letter-spacing: 0.05em;
		margin-left: 25px;
		position: relative;

		&::after {
			content: '';
			position: absolute;
			bottom: -10px;
			left: 0;
			width: 100%;
			height: 1px;
			background-color: $cWhite;
			transform: scaleX(0);
			transform-origin: 0 0;
			transition: transform 0.3s;
		}

		&:hover {
			&::after {
				transform-origin: 0 0;
				transform: scaleX(1);
			}
		}
	}
	&__hamburger {
		display: none;
	}

	&.inview {
		height: 64px;
		background-color: $cColorMain2;
	}
}

.p-upbtn-trigger {
	position: absolute;
	height: 350px;
}
